import { Component } from '@angular/core';
import { RouterModule, NavigationStart, Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app works!';
  navOpen: boolean = false;

  constructor(private router: Router){
    console.log("Website Author: James Clare");
    console.log("Iteration: 1.0.1");

    router.events.subscribe((val) => {
      this.closeNav(); 
      console.log(val instanceof NavigationStart) 
    });
  }

  openNav(): void{
    document.getElementById("sideNav").style.width = "250px";
    document.getElementById("menuIcon").style.transform = "rotate(90deg)";
    this.navOpen = true;
  }

  closeNav(): void {
    document.getElementById("sideNav").style.width = "0";
    document.getElementById("menuIcon").style.transform = "rotate(0deg)";
    this.navOpen = false;
  }

  toggleNav(): void {
    if (this.navOpen) { 
      this.closeNav(); 
    } else { 
      this.openNav();
    }
  }

}



